header {
  padding-top: 1.7rem;
  width: 100%;
  z-index: 2;
  right: 8%;
  gap: 0.8rem;
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  margin-top: -1.5rem;

}

.containerHeader {
  display: flex;
  max-width: 1200px;
  margin: auto;
  height: 130px;
}

.subBar {
  background: rgb(80 80 80 / 30%);
  height: 5px;
}

.subTitle {
  font-size: 1.4rem;
  font-weight: 400;
}

.content h2 {
  font-size: 19px;
}

.box {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: 200px;
  background: #000000a6;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0 20px 50px rgb(31, 32, 41);
  border: 2px solid var(--color-primary);
  color: white;
  padding: 20px;
}

.box:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  transition: 0.5s;
  pointer-events: none;
}

.box:hover:before {
  left: -50%;
  transform: skewX(-5deg);
}


.box .content {
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border: 1px solid #ffffff;
  padding: 20px;
  text-align: center;
  box-shadow: 0 5px 10px rgba(9, 0, 0, 0.5);

}

.box span {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
  right: 0;
  padding: 50px;
}

.box span:nth-child(1) {
  transform: rotate(0deg);
}

.box span:nth-child(2) {
  transform: rotate(90deg);
}

.box span:nth-child(3) {
  transform: rotate(180deg);
}

.box span:nth-child(4) {
  transform: rotate(270deg);
}

.box span:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background: #6d0000;
  animation: animate 4s linear infinite;
}

@keyframes animate {
  0% {
    transform: scaleX(0);
    transform-origin: left;
  }

  50% {
    transform: scaleX(1);
    transform-origin: left;
  }

  50.1% {
    transform: scaleX(1);
    transform-origin: right;

  }

  100% {
    transform: scaleX(0);
    transform-origin: right;
  }
}


/* ================ MEDIA QUERIES (MEDIUM DEVICES) ================= */
@media screen and (max-width: 1024px) {
  header {
    padding-top: 5rem;
  }

  .box {
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    height: 200px;
  }
}

/* ================ MEDIA QUERIES (SMALL DEVICES) ================= */
@media screen and (max-width: 600px) {

  .box {
    top: 100%;
    height: 200px;
  }

  .subBar {
    display: none;
  }
}